<style scoped lang="less">
  .add-template {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    background: #fff;
    .return-templatebtn {
      float: left;
      margin-left: 1.5%;
      margin-top: 15px;
    }
    .template-info {
      float: left;
      width: 100%;
      height: 85%;
      min-height: 500px;
      margin-top: 2%;
      padding: 0 3%;
      overflow-y: auto;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      h3 {
        margin-bottom: 10px;
        margin-right: 10px;
        display: inline-block;
      }
      span {
        vertical-align: -3px;
      }
      .ql-editor {
        min-height:300px;
        font-size: 16px;
        color: #666;
      }
      .return-templatebtn {
        float: right;
        margin-right: 20px;
      }
    }
  }
</style>
<template>
  <div class="templateMaintain content-layout">
    <div class="content-header">
      <div class="content-btn">
        <el-button type="info" size="small" @click="addtemplate">新建模板</el-button>
      </div>
      <header-search :seachData="seachData" @seach="seachFn"></header-search>
    </div>
    <div class="content-main">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="a" label="应用模块"></el-table-column>
        <el-table-column prop="b" label="模板标题"></el-table-column>
        <el-table-column prop="c" label="更新时间"></el-table-column>
        <el-table-column label="操作" width="200">
          <template scope="scope">
            <el-button type="text" size="small" @click.native.prevent="edittemplate(scope.$index, tableData)">编辑</el-button>
            <el-button type="text" size="small" @click.native.prevent="removetemplate(scope.$index, tableData)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="prev, pager, next, jumper"
          :total="1000">
        </el-pagination>
      </div>
    </div>
    <transition name="el-fade-in">
      <div class="add-template" v-show="showaddTemplate">
        <el-button class="return-templatebtn" type="primary" size="small" icon="arrow-left" @click="returntemplatebtn">返回</el-button>
        <div class="template-info">
          <h3><span>标题：</span><el-input v-model="editor.title" size="small" :style="{width: '200px'}" placeholder="请输入内容"></el-input></h3>
          <span>应用模块</span>
          <el-radio class="radio" v-model="editor.module" label="大体所见">大体所见</el-radio>
          <el-radio class="radio" v-model="editor.module" label="诊断意见">诊断意见</el-radio>
          <quill-editor ref="myTextEditor" v-model="editor.content" :config="editor.editorOption"></quill-editor>
          <el-button class="return-templatebtn" type="primary" size="small" @click="geteditorval">保存</el-button>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
  import { quillEditor } from 'vue-quill-editor'
  import headerSearch from '../../../public/header-search'
  import searchDataModule from '../../../../api/searchDataModule'
  import searchInitDefault from '../../../../api/searchInitDefault'
  export default {
    name: '',
    data(){
      return {
        seachData: {
          starttime: searchDataModule.starttime,
          endtime: searchDataModule.endtime,
          mkval: searchDataModule.mkval,
          mk: searchDataModule.mk,
          seachInfo: searchDataModule.seachInfo
        },
        tableData: [
          {a: '大体所见', b: '标题n', c: '2017-06-08'},
          {a: '诊断意见', b: '标题n', c: '2017-06-08'},
          {a: '大体所见', b: '标题n', c: '2017-06-08'},
          {a: '大体所见', b: '标题n', c: '2017-06-08'},
          {a: '诊断意见', b: '标题n', c: '2017-06-08'},
          {a: '大体所见', b: '标题n', c: '2017-06-08'},
          {a: '诊断意见', b: '标题n', c: '2017-06-08'},
          {a: '大体所见', b: '标题n', c: '2017-06-08'}
        ],
        currentPage: 1,//分页
        showaddTemplate: false,
        editor: {
          editorOption: {},
          content: '',
          title: '',
          module: '大体所见'
        },
      }
    },
    props: ['a'],
    components: {
      quillEditor,
      headerSearch
    },
    mounted(){
      searchInitDefault(searchDataModule);
    },
    methods: {
      seachFn(){
        console.log(this.seachData);
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      addtemplate(){
        this.showaddTemplate = true;
      },
      returntemplatebtn(){
        this.showaddTemplate = false;
        this.editor = {
          content: '',
          title: '',
          module: '大体所见'
        }
          /*新建数据为空*/
      },
      edittemplate(i,arr){
        this.showaddTemplate = true;
        /*编辑需要传递模板数据*/
        this.editor = {
          content: '<p>嘻嘻哈哈哈</p>',
          title: arr[i].b,
          module: arr[i].a
        }
      },
      removetemplate(i,arr){
        this.$confirm('此操作将永久删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          arr.splice(i, 1);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      geteditorval(){
        var newrow = {
          a: this.editor.module,
          b: this.editor.title,
          c: '2017-02-01'
        }
        this.tableData.push(newrow);

        this.showaddTemplate = false;
        this.editor.content = '';
        this.editor.title = '';
        this.$message({
          type: 'success',
          message: '保存成功!'
        });

//        this.$message({
//          type: 'info',
//          message: '保存失败'
//        });

//        console.log(this.editor.editorOption);
//        console.log(this.editor.content);
      }
    }
  }
</script>



